.formfield {
  box-sizing: border-box;

  display: block;
  width: 100%;
  height: 48px;

  padding: 14px 16px;

  border: 2px solid transparent;
  border-radius: 8px;
  background-color: #f7f8ff;

  color: #000;

  &::placeholder {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;

    color: #8f99a8;
  }

  &:hover {
    border-color: #ccd0e0;
  }

  &:focus {
    border-color: $colorBlue;
    outline: none;
  }

  &--textarea {
    height: 144px;
    resize: none;

    font-family: inherit;
  }
}
